<template>
  <div class="profile-wrapper">

      <yd-navbar title="Your profile">
            <router-link to="/setting" slot="left">
                <yd-navbar-back-icon>back</yd-navbar-back-icon>
            </router-link>
        </yd-navbar>
      <h1 class="bigtitle"></h1>



      <yd-cell-group title="Picture">
          
            <Vqiniu imgtype="profile"></Vqiniu>
          
      </yd-cell-group>

      <yd-cell-group title="Name">
          <yd-cell-item>
            <span slot="left"> </span>
            <yd-input slot="right" type="text" required max="20" placeholder=" Your name" v-model="userInfo.username"></yd-input>
          </yd-cell-item>
      </yd-cell-group>


       <yd-cell-group title="About me">
          <yd-cell-item>
                <yd-textarea slot="right" placeholder="tell a little about yourself" maxlength="200"></yd-textarea>
          </yd-cell-item>
      </yd-cell-group>

      <yd-cell-group title="What do you do?">
          <yd-cell-item>
                <yd-input slot="right" type="text" required max="20" placeholder=" " ></yd-input>
          </yd-cell-item>
      </yd-cell-group>

      <yd-cell-group title="Gender">
        <yd-cell-item arrow>
                <select slot="right">
                    <option value="1">male</option>
                    <option value="2">female</option>
                    <option value="3">others</option>
                </select>
        </yd-cell-item>
    </yd-cell-group>

    <yd-cell-group title="My ideal living location">
        <yd-cell-item arrow>
                <select slot="right">
                    <option value="1">guy</option>
                    <option value="2">girl</option>
                    <option value="3">other</option>
                </select>
        </yd-cell-item>
    </yd-cell-group>

    <yd-button-group>
        <yd-button size="large" @click.native="updateUserInfo" type="primary">Save</yd-button>
    </yd-button-group>

  </div>
</template>

<script>
import Vqiniu from '@/components/vqiniu'
import {APIuser,APIupdateuserinfo} from '../../api/api'
import cookie from '../../static/js/cookie'

import {Input,CellGroup,CellItem} from 'vue-ydui'
import {mapState} from 'vuex'
export default {
    components:{
        Vqiniu,
        CellGroup,
        CellItem,
        Input
    },
    data () {
        return {
            userInfo:{
                username:'',
                profile:[],
            }
        }
    },
    computed:{
        ...mapState(['userimg']),
    },
    methods: {
        
        getUserinfo () {
            this.userInfo.username = cookie.getCookie('username')
            APIuser(
                this.userInfo.username
            ).then( res => {
                this.userInfo = res.data
            })
        },
        updateUserInfo() {
            console.log(this.userimg)
            this.userInfo.profile  = this.userimg.join(',')
            APIupdateuserinfo(
                this.userInfo
            ).then( res => {
                this.res
            }).catch()
        },
        
    },
    created () {
        this.getUserinfo()
    },
    activated () {
        this.userimg = null
    }
}
</script>
<style lang="stylus" scoped>
.headimg
    width 100%
    height 200px
    .bigone
        width 60%
        background #dddddd
        height 220px
        display inline-block
    .smallone
        width 30%
        height 220px
        display inline-block
        .one
            height 100px
            margin-bottom 10px
            background #dddddd

</style>





